<template>
	<section>
		<Modal v-if="modalText !== ''" :text="modalText" v-on:close="modalText = ''"/>

		<form v-on:submit.prevent="createUser">
			<input v-model="newUser.name" type="text" placeholder="name" required tabindex="0">
			<input v-model="newUser.email" type="email" placeholder="email" tabindex="0" required>
			<input type="submit" @click="newUser.admin = true" value="Also Admin hinzuf√ºgen" class="button">
			<input type="submit" @click="newUser.admin = false" value="Also Nutzer hinzuf√ºgen" class="button">
		</form>

		<div class="user-search">
			<input type="text" placeholder="suche" tabindex="0">
			<div class="button" tabindex="0">üîé</div>
		</div>

		 <UserListing v-for="user in users" :user="user" :key="user.uid"/>	
	</section>
</template>

<script>
import UserListing from './UserListing'
import Modal from './Modal'
export default {
	components: {
		UserListing,
		Modal
	},
	props: ['token'],
	created() {
		fetch('/api/users', {
				method: "POST",
     		body: JSON.stringify({token: this.token}),
      	headers: {"Content-Type": "application/json"}
		})
		.then(data => data.json())
		.then(json => this.users)
	},
	data(){ return {
		users: [{"uid":"ft5k00OOdcPUIkiXffYlbaKIMWL2","email":"me@malts.me"},{"uid":"llVMChG6TaPBNxqZ0DfkGeGOk3Z2","email":"admin@lennard.tech"}],
		modalText: '',
		newUser: {
			email: '',
			name: '',
			admin: false
		},
		mailRegex: ''
	}},
	methods: {
		createUser() {
			let testRegex = /^(([A-Z]|[\u00C0-\u00DF])([a-z]|[\u00E0-\u00FF]|\u00DF)+ ?){2,}$/g;
			
			if(!testRegex.test(this.newUser.name)) {
				this.modalText = `Name im falschen Format`
				return;
			}

			fetch('/api/user', {
				method: "PUT",
     		body: JSON.stringify({
					 token: this.token,
					 name: this.newUser.name,
					 email: this.newUser.email,
					 admin: this.newUser.admin
				}),
      	headers: {"Content-Type": "application/json"}
			})
			.then(data => data.json())
			.then(json => {
				this.newUser.email = '';
				this.newUser.name = '';
				this.modalText = 'Nutzer erstellt';
			})
			.catch(e => this.modalText = 'Irgendwas ist mit der Anfrage falsch gelaufen')

		}
	}

}
</script>

<style lang="scss" scoped>
	section {
		background: #fff;

		> div:not(.modal), form > input{
			border-bottom: 1px solid #000;
		}
	}
	.user-search {
		display: flex;
		grid-template-columns: 4fr 1fr 1fr 1fr;

		input {
			flex-grow: 1;
		}
		.button {
			border-left: 1px solid black;
			padding-left: 5rem;
			padding-right: 5rem;
		}

	}
	.split {
		display: flex;
		border-bottom: 1px solid black;
		input:not(:last-child) {
			border-right: 1px solid black;
		}
	}
</style>



